@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css?family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans&display=swap);
.modal{position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,0.6);font-family:"Roboto Condensed", sans-serif}.modal .modal-main{position:absolute;background:#f4f3ef;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);height:97vh;width:75vw;top:50%;left:50%;box-shadow:0 2px 25px #000;border-radius:3px;box-sizing:border-box}.display-block{display:block}.display-none{display:none}.coinInfoContainer{-webkit-transition:0.5s;transition:0.5s;max-height:89vh;display:flex;margin-left:20px;margin-top:10px;margin-right:20px;flex-direction:column}.nameAndIcon{display:flex;justify-content:center;flex-direction:row;border-bottom:1px solid black}.description{margin-top:10px;margin-bottom:10px}.currentPrice{display:flex;margin-top:10px;margin-bottom:10px;border:1px solid #2789d34d;box-shadow:6px 5px 5px #d2c5c5;border-radius:5px;padding:5px;height:100px;width:250px;background:#f7f8fa}.priceInfo{margin-left:35px;text-align:right;margin-right:15px}.highestPrice{display:flex;margin-top:10px;margin-bottom:10px;border:1px solid #2789d34d;box-shadow:6px 5px 5px #d2c5c5;border-radius:5px;padding:5px;height:100px;width:250px;background:#f7f8fa}.circulatingSupply{display:flex;margin-top:10px;margin-bottom:10px;border:1px solid #2789d34d;box-shadow:6px 5px 5px #d2c5c5;border-radius:5px;padding:5px;height:100px;width:300px;background:#f7f8fa}.totalSupply{display:flex;margin-top:10px;margin-bottom:10px;border:1px solid #2789d34d;box-shadow:6px 5px 5px #d2c5c5;border-radius:5px;padding:5px;height:100px;width:300px;background:#f7f8fa}.iconHolders{display:flex;flex-direction:row;justify-content:space-evenly;flex-wrap:wrap}.cirHolder{display:flex;flex-direction:column}.break{flex-basis:100%;height:0}.iconDisplay{margin-top:15px;margin-left:15px}.infoTxt{font-size:22px}.graphHolder{max-height:42vh}.chartjs-render-monitor{max-width:75vh !important;max-height:40vh !important;margin:0 auto !important}.allCoins{margin-left:45px}

.coinList {
  width: 150px;
  height: 50px;
}

.coinList:hover {
  cursor: pointer;
}

.coinHolder {
  width: 150px;
  height: 50px;
}

.coinSymbol {
  height: 50px;
  width: 50px;
  margin-left: 30px;
}

.allCoins {
  display: flex;
  flex-wrap: wrap;
}

li {
  list-style-type: none;
}

.nameAndPic {
  display: flex;
  justify-content: space-between;
  margin-top: 13px;
  margin-left: 15px;
}

.iconAndGraph {
  max-height: 70vh;
}

.search{margin:20px}.search>div{display:inline-block;position:relative}.search>div:after{content:"";background:black;width:4px;height:20px;position:absolute;top:40px;right:2px;-webkit-transform:rotate(135deg);transform:rotate(135deg)}.search>div>input{color:black;font-size:16px;background:transparent;width:25px;height:25px;padding:10px;border:solid 3px black;outline:none;border-radius:35px;-webkit-transition:width 0.5s;transition:width 0.5s}.search>div>input::-webkit-input-placeholder{color:black;opacity:1}.search>div>input:-ms-input-placeholder{color:black;opacity:1}.search>div>input::-ms-input-placeholder{color:black;opacity:1}.search>div>input::placeholder{color:black;opacity:1}.search>div>input::-ms-placeholder{color:black}.search>div>input::-ms-input-placeholder{color:black}.search>div>input:focus,.search>div>input:valid{width:250px}.coinHolders{border:1px solid black;width:150px}.mainCoins{display:flex;flex-direction:row;height:90px;width:200px;margin:5px;border:1.2px solid #791bd5;border-radius:3px}.coinNameText{font-family:"Varela Round", sans-serif;margin-left:10px}

.title{font-family:"Josefin Sans", sans-serif}.buttonsHolder{width:100%;display:flex;justify-content:space-evenly;font-family:"Josefin Sans", sans-serif}.queryButtonStyles{border-radius:5px;height:45px;border:1px solid #791bd5;font-size:15px;margin-left:7px;margin-right:7px}.queryButtonStyles:hover{cursor:pointer}.toggleButtons:hover{cursor:pointer}

